
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {mapMutations} from 'vuex'
import {time} from '../config'
export default {
  name: 'register',
  head: {
    title: {
      inner: '用户注册'
    }
  },
  data () {
    return {
      isReg: false,
      loading: false, // 注册按钮显示加载
      getting: false, // 获取验证码显示加载
      show: false, // 倒计时
      start: false,
      time: time,
      smsType: 2,
      text: '发送验证码',
      pwd: '',
      form: {
        tel: '',
        name: '',
        captcha: '',
        pwd: ''
      }
    }
  },
  components: {
    Group,
    Cell,
    XInput,
    XButton,
    Countdown
  },
  methods: {
    handleTips () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        position: 'bottom',
        text: this.time + '秒后才能重新获取！',
        time: '1000'
      })
    },
    handleTime () {
      this.show = false
      this.start = false
      this.time = time
    },
    handleSendSms () {
      this.postSMS(this)
    },
    handleRegister () {
      this.postRegister(this)
    },
    checkPwd () {
      if (!this.$refs.pwd.valid) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '密码必须大于6位！',
          time: '1000'
        })
      }
    },
    reCheckPwd () {
      if (this.pwd !== this.form.pwd) {
        this.$vux.toast.show({
          type: 'text',
          width: '20em',
          position: 'bottom',
          text: '两次输入密码不同！',
          time: '1000'
        })
      }
    },
    ...mapMutations({
      postRegister: 'postRegister',
      postSMS: 'postSMS'
    })
  }
}



